@charset "utf-8";

/* 메인 css */

/* 에필로그 폰트 */
/* 전통적인 북 타이포 스타일 */
@import url('https://fonts.googleapis.com/css2?family=Libre+Baskerville:ital,wght@0,400;0,700;1,400&family=Nanum+Pen+Script&display=swap');

/* 애니메이션 키프레임 */
@keyframes blink {0%, 100% {opacity: 1;} 50% {opacity: 0;}}
@keyframes moveDot {0% {top:4px; opacity: 1;} 50% {top:12px; opacity: 0.5;} 100%{top:4px; opacity: 1;}}
@keyframes fadeIn { to {opacity: 1;}}
@keyframes shake {
  0% { transform: translate(0, 0); }
  1%, 3%, 5%, 7%, 9% { transform: translate(-2px, 0); }
  2%, 4%, 6%, 8%, 10% { transform: translate(2px, 0); }
  11% { transform: translate(0, 0); }
  100% { transform: translate(0, 0); }
}
/* 공통서식 */
:root{
  --text_color:#222;
  --main_color_navy:#0A192F;
  --main_color_yellow:#F9CA24;
  --main_color_mint:#A2DED0;
  --main_font:"Libre Baskerville";
  --sub_font:"Nanum Pen Script";
}

section{
  width: 100%;
  height: 100vh;
  margin: 0 auto;
}
h2{font-family: var(--main_font);}
h3{font-family: var(--main_font);}
/* Intro */
.intro{
  width:100%; height: 100vh;
  position: relative;
  background: url(../../public/images/intro_bg.png);
  background-repeat: no-repeat;
  z-index: 1;
}
.intro h2{
  font-family: var(--main_font);
  font-size: 10rem;
  text-align: left;
  color: #fff;
}
/* h2 텍스트 바 서식 */
.intro .text_bar{display: inline-block; margin-left: 2px; color: #fff; animation: blink 0.8s infinite;}

/* intro right 이미지 */
.r_img{position: absolute; top:0px; right: 0px;}

/* intro TEXT 서식 */
.intro_text{
  top: 20%; left: 10%;
  font-size: 8.75rem;
  font-family: var(--main_font);
}
.intro_text p{position: absolute;}
.intro_text p:first-child{left: 10%; color: #ffbfbf;}
.intro_text p:nth-child(2){font-size:7.5rem; left: 20%; top: 37%; color: #A2DED0;}
.intro_text p:nth-child(3){font-size: 6.25rem; top: 50%; left: 30%; color: #2EE59D;}
.intro_text p:last-child{font-size: 5rem; top: 60%; left: 35%; color: var(--main_color_yellow);}

.fade-in{
  opacity: 0;
  animation: fadeIn 1s ease-in forwards;
}
.fade-in.d1{animation-delay: 0.5s;}
.fade-in.d2{animation-delay: 1s;}
.fade-in.d3{animation-delay: 1.5s;}
.fade-in.d4{animation-delay: 2s;}

/* 알림 텍스트 */
.subtext{
  position: absolute ; bottom: 25%; right: 30px; color: #fff; font-weight: normal;
  white-space: pre-line;
  word-break: keep-all;
  text-align: left;
}


/* scroll down 서식 */
.scroll-down{
  position: absolute;
  bottom:8rem;
  left: 50%;
  transform: translateX(-50%);
  text-align: center;
  color: #fff;
  font-family: var(--sub_font);
}
.scroll-down p{color: #fff; font-family: var(--main_font);}
.scroll-down p:last-child{
  width: 5px 10px;
  color: #fff; animation: moveDot 1.5s infinite;
position: absolute; top: 30px; left: 50%; transform: translateX(-50%);}
.mouse{
  width: 24px;
  height: 40px;
  border: 2px solid #fff;
  border-radius: 14px;
  margin: 0 auto 8px;
  position: relative;
}
.dot{
  position: absolute;
  width: 4px; height: 8px;
  background: #fff;
  border-radius: 50%;
  top: 4px; left: 50%;
  transform: translateX(-50%);
  animation: moveDot 1.5s infinite;
}
.dot-bottom{position: absolute; width: 24px; height: 1px;
border-bottom: 1px solid #fff; top:20px; text-align: center;}


/*Profile 서식 */
.profile{background:#232323; margin-top: -100px; position: relative; top: 0; left: 0; width: 100%; height: 100vh; z-index: 10; pointer-events: auto;}
.profile h2{font-size: 50px; color: #fff;}
.profile_img{margin-top: 30px; width: 30%; perspective: 1000px;  cursor: pointer; }
.profile_img:hover{animation: shake 3s steps(1, end) infinite;}
.profile_img img{width: 200px; margin-left: 33%; border-radius: 10px; height: 300px; }
.flipper {
  position: relative; width: 100%; height: 100%;
  transition: transform 0.6s ease;
  transform-style: preserve-3d;
}
.flipper.flip{  transform: rotateY(180deg);}
.front, .back {
  backface-visibility: hidden;
  position: absolute;
  object-fit: cover;
  width: 100%;
  height: 100%;
}
.back {
  transform: rotateY(180deg);
}

.profile_text{margin-top: 18%; margin-left: 8%; width: 400px;}
.profile_text p{
  text-align: left; color: #fff; font-size: large; line-height: 50px; margin-left: 10%; padding: 0px 5px;}
.profile_text .profile_name{font-weight: bold; font-size: 24px; padding-bottom: 20px; color: #fff;padding-left: 100px;}
.fa-baby{padding-right: 20px;}
.fa-house{padding-right: 15px;}
.fa-mobile{padding-right: 23px;}
.fa-envelope{padding-right:18px ;}
.profile_text p span{margin: 0px 3px;}
.profile_text p span, .profile_text p span a{background: rgb(104, 212, 255); padding: 4px 7px; border-radius: 10px; color:#fff;}
.profile_text p .music:hover{animation: shake 3s steps(1, end) infinite;}


.profile_r{position: absolute; top: 130px; left:35%;}
.profile_r h3{color: #fff; font-size:2rem; font-family: var(--main_font);}

/* profile skill 서식 */
.skill{display: flex; justify-content: center; margin-bottom: 50px;}
.skill li{padding: 0px 5px; text-align: center; color: #fff; font-weight:400;}

/* profile education 서식 */
.education{line-height: 30px;}
.education li{font-size: 16px; color: #fff;list-style:disc;}

/* profile about 서식 */
.about{margin-top: 50px;}
.about div{background: #fff; width: 630px; text-align: center;
padding: 30px 30px; font-size: 15px; border-radius: 10px; box-shadow:  0 0 10px rgba(0,0,0,0.6);}
.about div p{font-weight: bold;}
.about div p span:first-child{color:#2EE59D; font-size: 20px;}
.about div p span:last-child{color: #FF6F61; font-size: 20px;}

/* profile data 서식 */
.data{position: absolute; top:35%; right: -20%; color: #222;}


/* Project 서식 */
.project{background: #232323; position: relative; height: 320vh; padding-bottom: 100px; grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));}
.project h2{color: #fff; font-size: 50px; margin-bottom: 20px;}
.project .more{color: #fff; font-size: 30px; position: absolute; top:2%; right: 100px; transition: 0.2s;}
.project .more:hover{color: var(--main_color_yellow); padding-bottom:3px; border-bottom: 2px solid var(--main_color_yellow);}
/* project 카드, 링크, 이미지 서식 */
.cardstyle{background: #ccc; border-radius: 12px; padding: 12px; box-shadow: 0 8px 20px rgba(0,0,0,0.1); transition: transform 0.3s; height: 70%; width: 700px; margin: 50px auto 150px auto; overflow: hidden;}
.cardinner{background-color:#f9f9f9; width: 480px; height: 580px; margin: 0 auto; padding: 10px;}

.linkstyle{color: inherit;}
.imgstyle{width: 100%; border-radius: 8px; margin-bottom: 12px;}


/* contact 서식 */
.contact{width: 100%; height: 90vh; background: url(../../public/images/contact_bg.png);}
.contact h2{font-size: 50px; color: var(--main_color_yellow); font-family: var(--main_font);}
.contact_container{background: #fff; width: 40%; height: 250px; float: right; border-radius: 30px 0 0 30px; transform: translateY(-400px);  box-shadow: 0 5px 10px rgba(0,0,0,0.5);}
.contact_container ul{display: flex; justify-content: space-around; line-height: 50px;}
.contact_container ul li{font-family: var(--main_font);}
.contact_container ul li:first-child a{font-size: 50px; color:#000; padding-right: 10px;}
.contact_container ul li:first-child img{transform: translateY(10px);}
.contact_container ul li h3{font-size: 30px; margin-top: 60px;}
.contact_container ul li p, .contact_container ul li a{font-size: 24px;}

/* 에필로그 서식 */
.epilogue{width: 40%; height: 530px; background: #fff; transform: translateY(80px); border-radius: 0px 30px 30px 0px; box-shadow: 5px 0 10px rgba(0,0,0,0.5);}
.epilogue h3{font-size: 30px; font-family: var(--main_font);
padding: 20px 0px 0px 10px; background: #A2DED0; width: 125.4%; border-radius: 0px 30px 0 0; padding-bottom: 10px; color: #fff;}
.epilogue div{width: 600px;}
.epilogue div p{font-size: 30px; line-height: 30px; text-indent:10px; padding:30px 0px 0px 100px; font-family: var(--sub_font);}
.epilogue div p span{color:#F76707; font-weight: bold;}

/* sns서식 */
.sns{width: 100px; height: 120px; background: #fff; position: relative; bottom: 10%; left: 95%; border-radius: 30px 0 0 30px; box-shadow: 0 5px 10px rgba(0,0,0,0.5);}
.sns div a .insta{width: 105px;}
.sns div a .kakao{margin-left: 40px; width: 30px;}