@charset "utf-8";

/* 에필로그 폰트 */
/* 전통적인 북 타이포 스타일 */
@import url('https://fonts.googleapis.com/css2?family=Libre+Baskerville:ital,wght@0,400;0,700;1,400&family=Nanum+Pen+Script&display=swap');
/* 공통서식 */
:root{
  --text_color:#222;
  --main_color_navy:#2c3e50;
  --main_color_yellow:#F5CC5E;
  --main_color_mint:#A2DED0;
  --main_font:"Libre Baskerville";
  --sub_font:"Nanum Pen Script";
}
*{margin: 0; padding: 0;}
body{  
  font-size: 14px;
  font-family: '맑은 고딕', sans-serif, Arial;
  color: var(--text_color);
  overflow-x: hidden;
}
ol, li, ul{list-style: none;}
a{color: var(--text_color); text-decoration: none;}
h2{padding: 20px 0px 0px 20px;}
/* 헤더 */
header{
  width: 100%;
  height: 100px;
  background: var(--main_color_navy);
  position: fixed;
  left: 0px; top: 0px;
  color: #fff;
  z-index: 100;
}
header h1{
  float: left;
  color: #fff;
  line-height: 15px;
  padding: 20px 0px 0px 10px;
}
header h1 a{color: #fff; font-family: var(--main_font); font-weight: bold; font-size: 1.6rem;}
header nav{
  float: right;
  width: 60%;
}
header nav ul{
  display: flex;
  justify-content: right;
  margin-right: 20px;
}
header nav ul li{line-height: 100px;}
header nav button{
  border: none;
  background: none;
  font-size: 1.4rem;
  cursor: pointer;
  color: #fff;
  font-weight: bold;
  transition: 0.1s;
  padding: 0px 8px;
  font-family: var(--main_font);
}
header nav button:hover{color:var(--main_color_yellow); 
border-bottom: 2px solid var(--main_color_yellow);padding:3px 8px;}

/* 탑버튼 */
.top_btn button{
  border: none;
  border: 1px solid #969696;
  background: var(--main_color_navy);
  color: #fff;
  padding: 15px;
  border-radius: 50%;
  position: fixed;
  bottom: 30px; right: 30px;
  z-index: 100;
}
.top_btn button:hover{background:#1e2a36; cursor: pointer ;}

