@charset "utf-8";

/* 프로젝트 좌우 슬라이드 */
.slide-wrapper{
  display: flex; justify-content: space-between;align-items: center; margin: 150px 0; padding: 0 10%; gap:40px; flex-direction: row;
}

.slide-img{width: 100%; height: 500px;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  position: relative; overflow: hidden; z-index: 11;

}
.slide-img img{width: 80%; height: auto; position: absolute; left: 50%; top: 50%; transform: translate(-50%,-50%); pointer-events: none; }

.slide-text{width: 800px; background: #fff; padding: 20px; border-radius: 5px;}
.slide-text img{height:50px;}
.slide-text a:first-child{color: #232323;float: right; font-weight: bold; font-size: 20px; padding: 3px 5px; border-radius: 5px; transition: 0.3s;}
.slide-text a:first-child:hover{background: #a8a8a8; }
.slide-text p{color: #222; font-size: 20px; line-height: 30px; padding-bottom: 20px; font-family: "Nanum Gothic";}
.slide-text p span{font-weight: bold;}
.slide-text p:last-child{margin-top:-20px;}

.slide-text .notion_btn button{border: none; background: var(--main_color_navy); color: #fff; padding: 5px 10px; border-radius: 5px; width: 100%; cursor: pointer; transition: 0.5s;}
.slide-text .notion_btn button:hover{transform: translateY(-5px); color: var(--main_color_yellow);}

